
<template>
  <div class="error403">
    测试
  </div>
</template>

<script>
export default {
  name: 'Error403',
  methods: {
    backPage () {
      this.$router.go(-1)
    },
    goHome () {
      this.$router.push({
        name: 'home_index'
      })
    }
  },
   beforeRouteEnter(to, from, next) {
     next(vm => {
       //判断固定路由的特殊情况
       let arr = [
         "live-manage-channel-new",
         'details-dvb',
         'details-live-manage-channel',
         'user-information',
         'details-usergroup',
         "details-dvb",
         'live-manage-epgDetails',
         "live-manage-channel-new",
         'player-flow',
         'subcollectionList',
         'check-trailer',
         'check-all'
       ]
       if(arr.includes(from.name)){
         vm.$store.commit('addOpenSubmenu1',from.name)
       }
       vm.$router.replace({
         name:from.name
       })
    });
},
}
</script>
